<template>
  <transition name="fade" mode="out-in">
    <div class="comment-item" v-if="hasReply">
      <div class="comment-header">
        <router-link class="comment-user" to="/user">Bob</router-link>
      </div>
      <div class="comment-content">Shut up!</div>
      <div class="comment-footer">
        <el-button @click="hasReplyBox = true">回复</el-button>
        <el-button @click="hasReply = false">删除</el-button>
      </div>
      <div class="comment-line"></div>
      <transition name="fade" mode="out-in">
        <div class="comment-box" v-show="hasReplyBox">
          <el-input
            data-vue-emojiable="true"
            type="textarea"
            :rows="5"
            placeholder="来点评论吧"
            v-model="commentText"
          ></el-input>

          <div class="toolbar">
            <div class="left">
              <i class="icon-ali-bq"></i>
            </div>
            <!-- <vue-feedback-reaction v-model="feedback" /> -->
            <div class="right">
              <el-button @click="deliverComment">发表</el-button>
              <el-button @click="hasReplyBox = false" transition="fade">取消</el-button>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
export default {
  data() {
    return {
      commentText: "",
      hasReply: true,
      hasReplyBox: false
    };
  },
  methods: {
    //comment box
    deliverComment() {}
  }
};
</script>

<style>
/* .comment-item {
  padding: 10px 0;
} */
/* .comment-header {
  margin: 10px 0;
}
.comment-user{
  color: #000;
}
.comment-content {
  font-size: 14px;
  margin: 10px 0;
}
.comment-footer {
  margin: 10px 0;
}
.comment-line{
  margin: 15px 0;
} */

/* comment */
/* .comment-box .el-textarea >>> .el-textarea__inner{
  margin: 10px 0;
} */
.comment-box .toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.comment-box .left i {
  font-size: 20px;
  color: #d0d0d0;
}
.comment-box .left i:hover {
  color: #aaa;
  cursor: pointer;
}
.comment-box .right {
  display: flex;
  align-items: center;
}

.el-button {
  height: 30px;
  padding: 5px 20px;
}
.el-button:hover {
  border-color: #81d4fa;
  color: #81d4fa;
}

/* fade in out transition */
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
</style>



